import React, { useMemo } from 'react'
import { useTooltip, TooltipWithBounds } from '@vx/tooltip';
import { localPoint } from '@vx/event';
import { AreaClosed, LinePath } from '@vx/shape';
import { ScaleTime, ScaleLinear } from 'd3-scale';
import { moment, Moment, FORMAT_DATETIME_2 } from '../../utils/time';
import { TimeSeries } from '../../model/domain/metrics';
import Line from '@vx/shape/lib/shapes/Line';
import { curveStep as curve } from '@vx/curve';
import { LinearGradient } from '@vx/gradient';
import useRandom from '../../hooks/random';
import { primaryColor } from '../../config';
import { GridRows } from '@vx/grid';
import {Text, Grid } from '../../style';

const colors = [primaryColor, "#004FFF", "#F26CA7", "#00F3F3", "#E71D36", "#C5D86D", "#2C634E"];
export const accentColor = "lightgray";
export interface DrawAreaProps {
  backgroundColor: string;
  width: number;
  height: number;
  timeSeriesResult: TimeSeries.Result,
  metricDescriptor: TimeSeries.MetricDescriptor,
  dateScale: ScaleTime<number, number>
  yScale: ScaleLinear<number, number>
}

interface TooltipData {
  timestamp: Moment;
  legend: { label: string, value: string, color: string }[];
}

const reducer = (datum: TimeSeries.Point, index: number, data: TimeSeries.Point[]): boolean => !datum.isMissing;

export function DrawArea({ width, height, timeSeriesResult, metricDescriptor, dateScale, yScale, backgroundColor }: DrawAreaProps) {
  const ttip = useTooltip<TooltipData>();

  //index is the ith TimeSeries
  const getColor = (index: number) => colors[index % colors.length];

  function handleTooltip(event) {

    //x and y coordinates relative to the svg's (0, 0) where this event was raised
    //TODO: memoize (x, y) -> { tooltipData, tooltipLeft, tooltipTop }
    const { x, y } = localPoint(event);

    //given the date, figures out the ith position in the timeSeries.points that is to be show in the label
    const bisect = (t: Moment) => {
      let points = timeSeriesResult.timeSeries[0].points;
      for (let i = points.length - 1; i >= 0; i--) {
        const at = points[i].at;
        if (t.isSameOrBefore(at)) {
          return i;
        }
      }
      return 0;
    }

    function getLegend(i: number) {
      const timeSeries = timeSeriesResult.timeSeries;

      const toPercent = (n: number) => (n * 100).toFixed(2) + '%';

      const legend = timeSeries.reduce((acc, ts, index) => {
        const p = ts.points[i];

        if (!p.isMissing) {
          const label = timeSeriesResult.crossSeriesReducer === 'NONE'
            ? ts.displayLabel
            : metricDescriptor.displayName;

          const value = timeSeriesResult.unit === '%'
            ? toPercent(p.value)
            : p.value + '';

          acc.push({ label, value, color: getColor(index) });
        }

        return acc;
      }, []);

      return legend;
    }

    const timestamp = moment(dateScale.invert(x));
    const i = bisect(timestamp);
    const legend = getLegend(i);

    ttip.showTooltip({
      tooltipData: { timestamp, legend },
      tooltipLeft: x,
      tooltipTop: y,
    });
  }

  //TODO: comment and explain why this is necessary
  const dataPoints: TimeSeries.Point[][] = useMemo(() => {
    const k = timeSeriesResult.alignmentPeriod;
    return timeSeriesResult.timeSeries.map(ts => {
      const pairedPoints = ts.points.reduce((pairedPoints, point) => {
        const pointPair = {
          value: point.value,
          isMissing: point.isMissing,
          at: moment(point.at).subtract(k, 'minutes')
        }

        pairedPoints.push(point);
        pairedPoints.push(pointPair);
        return pairedPoints;
      }, []);

      return pairedPoints;
    });
  }
    , [timeSeriesResult]);

  const linePathX = (d: TimeSeries.Point) =>  dateScale(moment(d.at));
  const linePathY = (d: TimeSeries.Point) =>  yScale(d.value);
  
  const { random } = useRandom({})
  const scribble = timeSeriesResult.crossSeriesReducer === 'NONE'
    ? (
      dataPoints.map((points, index) => (
        <LinePath<TimeSeries.Point>
          x={linePathX}
          y={linePathY}
          data={points}
          key={index}
          defined={reducer}
          strokeWidth={2}
          stroke={getColor(index)}
          curve={curve}
        />
      ))
    )
    : <>
      <LinearGradient id={"area-gradient" + random} from={accentColor} to={accentColor} toOpacity={0.3} />
      <AreaClosed<TimeSeries.Point>
        data={dataPoints[0]}
        x={linePathX}
        y={linePathY}
        yScale={yScale}
        strokeWidth={3}
        defined={reducer}
        stroke={"url(#area-gradient" + random + ")"}
        fill={"url(#area-gradient" + random + ")"}
        curve={curve}
      />
    </>

  return (
    <>
      <svg width={width} height={height}>

        <rect
          x={0}
          y={0}
          width={width}
          height={height}
          fill={backgroundColor} />

        <GridRows<number>
          scale={yScale}
          width={width}
          strokeDasharray="3,3"
          stroke={accentColor}
          strokeOpacity={0.3}
          pointerEvents="none"
        />

        {scribble}

        <rect
          x={0}
          y={0}
          width={width}
          height={height}
          fill={'transparent'}
          onTouchStart={handleTooltip}
          onTouchMove={handleTooltip}
          onMouseMove={handleTooltip}
          onMouseLeave={() => ttip.hideTooltip()} />
          
        {
          ttip.tooltipData &&
          <g>
            <Line
              from={{ x: ttip.tooltipLeft, y: 0 }}
              to={{ x: ttip.tooltipLeft, y: height }}
              stroke={primaryColor}
              strokeWidth={2}
              pointerEvents="none"
              strokeDasharray="5," />
          </g>
        }
      </svg>

      {
        ttip.tooltipOpen &&
        <TooltipWithBounds 
          top={ttip.tooltipTop}
          left={ttip.tooltipLeft}>
          <TooltipContent data={ttip.tooltipData} />
        </TooltipWithBounds>
      }
    </>
  )
}

function TooltipContent({ data }: { data: TooltipData }) {
  return (
    <Grid placeItems="start" customRows="auto 1fr" gridGap=".4em">
      <Text bold>{data.timestamp.format(FORMAT_DATETIME_2)}</Text>
      {
        data.legend.map(({ color, label, value }, index) => (
          <div key={index} style={{ padding: 2 }}>
            <span className='round' style={{ background: color }}></span>&nbsp;
            <b>{label}:</b>&nbsp;{value}
          </div>
        ))
      }
    </Grid>
  );
}