import React, { useState, useContext } from 'react';
import { Grid, Switch, Button } from '../../style';
import Layout from '../../layout';
import Listings from './listings';
import Analytics from './analytics';
import bg from '../../assets/login-bg.svg';
import { ComponentsContext } from '../../context/components';
import OccupancyTable from './occupancy-table';
import { useWidgets } from "../../hooks/widget";
import ScoreCards from "../../components/score-card/score-cards";
import { useSelector } from 'react-redux';

const activePages = {
  "ANALYTICS": "ANALYTICS",
  "LISTINGS": "LISTINGS",
  "OCCUPANCY": "OCCUPANCY"
};

const Garage = () => {
  const [activePage, setActivePage] = useState(activePages.ANALYTICS);
  const { editMode, toggleEditMode } = useContext(ComponentsContext);
  const eId = useSelector((state: any) => state.user.enterprise.id);
  const { loading, scoreCards, scoreCardOptions, editScoreCards, removeScoreCard } = useWidgets({ eId, screen: "garage", resource: "garage" });
  const style =    { maxWidth: "1500px", minWidth: "85%" };
  return (
    <Layout sidebar background={bg}>
      <Grid
        className="left-on-desktop"
        style={{ minHeight: "100vh" }}
        width="100%"
        placeItems="start center"
        customRows="auto 1fr"
      >
        <Grid mobileLayout={false} customCols="auto 1fr auto"  style={style}>
          <div></div>
          <Switch
            style={{ placeSelf: "stretch center" }}
            items={Object.keys(activePages)}
            onClick={(active: string) => setActivePage(active)}
            active={activePage}
          />
          <Button
            rounded
            shadow
            onClick={toggleEditMode} minWidth="4.5em">
            <i className={`fa fa-${!editMode ? 'edit' : 'check'}`} ></i>
          </Button>
        </Grid>
        <Grid
          placeItems="start stretch"
          height="100%"
          customRows="auto 1fr"
          style={style}
        >     
        <ScoreCards
            loading={loading}
            eId={eId}
            removeScoreCard={removeScoreCard}
            editScoreCards={editScoreCards}
            scoreCards={scoreCards}
            scoreCardOptions={scoreCardOptions}
          />

            {activePage === activePages.LISTINGS ? <Listings /> :
            activePage === activePages.ANALYTICS ? <Analytics /> : <OccupancyTable />}
        </Grid>
      </Grid>
    </Layout>
  );
}

export default Garage;
