import React, { Component } from "react";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { ScoreCard } from "./score-card";
import { ScoreCard as ScoreCardType } from "../../model/domain/dashboard";

const reorder = (list : any[], startIndex : number, endIndex: number) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};

export default class DraggableArea extends Component<any, any> {
  constructor(props : any) {
    super(props);
    this.onDragEnd = this.onDragEnd.bind(this);
  }

  onDragEnd(result : any) {
    if (!result.destination) {
      return;
    }

    const items = reorder(
      this.props.items,
      result.source.index,
      result.destination.index
    );
    this.props.setItems(items)
  }

  render() {
    return (
      <DragDropContext onDragEnd={this.onDragEnd}>
        <Droppable direction="horizontal" droppableId="droppable">
          {(provided : any) => (
            <div
            className="grid mobile-panel"
            style={{gridTemplateColumns: "repeat(5, 1fr)", width: '100%', placeItems: "stretch", height: "100%",minHeight: "150px"}}
              {...provided.droppableProps}
              ref={provided.innerRef}
            >
              {this.props.items.map((item : ScoreCardType, index : number) =>{ 
                      if(!item) return null;
                  const { title, backgroundColor } = item;
                     
                return(
                <Draggable key={JSON.stringify(item)} draggableId={JSON.stringify(item)} index={index}>
                  {(provided : any, snapshot : any) => (
                    <div
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      {...provided.dragHandleProps}    
                    >
                      <ScoreCard 
                        name={title} 
                        value={10} 
                        flatValueQuery={item.flatValueQuery}
                        bg={backgroundColor} 
                        editMode={this.props.editMode} 
                        remove={this.props.remove} 
                        index={index} 
                      />
                    </div>
                  )}
                </Draggable>
              )})}
              {provided.placeholder}
              {this.props.children}
            </div>
          )}
        </Droppable>
      </DragDropContext>
    );
  }
}

    