import { useState, useEffect } from "react";
import API from "../api";
import { TimeSeries } from "../model/domain/metrics";
import { moment, Moment } from '../utils/time';

const now = moment().toISOString();
const start = moment().add(-1, 'day').toISOString();

//NOTE: this should be kept in sync with default `3D` selection that you see in Tabs 
const defaultInterval = () => {
      const endTime = moment();
      const startTime = endTime.clone()
        .set('hours', 0)
        .set('minute', 0)
        .set('second', 0)
        .set('millisecond', 0);

      return { endTime : endTime.toISOString(), startTime: startTime.toISOString() };
  };



interface MetricDataStatus {
  error: boolean;
  loading: boolean;
  data: {
    timeSeriesResult: TimeSeries.Result | null;
    metricDescriptor: TimeSeries.MetricDescriptor;
  } | null;
}

export const useMetricData = (props: TimeSeries.PartialQuery, interval = defaultInterval()) => {
  const [state, setState] = useState<TimeSeries.Query>({...props, interval});
  const {
    interval: { startTime, endTime },
  } = state;
  
  const [metricDataStatus, setMetricDataStatus] = useState<MetricDataStatus>({
    error: false,
    loading: true,
    data: null 
  });

  const setInterval = (interval)=> setState({...state, interval });
 
  const setEndTime = (newEndtime: Moment) => {
    setState({
      ...state,
      interval: getInterval(moment(startTime), newEndtime)
    });
  };

  const setStartTime = (newStartTime: Moment) => {
    setState({
      ...state,
      interval: getInterval(newStartTime, moment(endTime)),
    });
  };

  const getInterval = (start: Moment, end: Moment) => {
    const shouldSwap = start > end;
      return {
        startTime : (shouldSwap? end: start).toISOString(),
        endTime : (shouldSwap? start : end).toISOString()
      };
  };

  const getMetricData = async (query: TimeSeries.Query) => {
    try {
      setMetricDataStatus({data : null, loading: true, error: false});

      const [{ timeSeriesResult }, { metricDescriptor }]  = await Promise.all(
        [API.getTimeSeries(query), API.getTimeSeriesMetricDescriptor(query.metricType)]);
        
      setMetricDataStatus({
        loading: false,
        error: false,
        data: {
          timeSeriesResult,
          metricDescriptor
        }
      });    
    } catch (e) {
      setMetricDataStatus({
        loading: false,
        error: true,
        data: null,
      });
    }
  };

 useEffect(() => {getMetricData({...state})}, [state]);
 useEffect(() => setState({...state, ...props}), [props]);
  return {
    metricDataStatus,
    interval: state.interval,
    setEndTime,
    setStartTime,
    setInterval
  };
};

export default { useMetricData };