import React, { useContext, } from 'react'
import { Card,  Button, Modal } from '../../style';
import { ComponentsContext } from '../../context/components';
import AddPanel from './score-card-adder';
import DraggableArea from './dragable';
import { gray } from '../../config';

const intoHeadings = (str: string): string => {
  if (!intoHeadings || intoHeadings.length === 0) throw new Error("");
  return str.charAt(0).toUpperCase() + str.substring(1).replace(/([a-z](?=[A-Z]))/g, '$1 ')
}

const ScoreCards = ({ scoreCards, editScoreCards, scoreCardOptions,loading, removeScoreCard} : any) => {
  const { editMode } = useContext(ComponentsContext);

  const closeAddModal = () => addModal?.close();

  const addModal = Modal({
    title: "Add",
    hideCancel: true,
    message: <AddPanel
      currentScoreCard={scoreCards}
      close={closeAddModal}
      setCurrent={editScoreCards}
      options={scoreCardOptions || []}
    />
  });

  return (
    <div className="slide-up" style={{background: gray, width: "100%", gridColumn: "1/-1", padding: ".2em .5em"}}>
      <DraggableArea items={scoreCards} setItems={editScoreCards} remove={removeScoreCard} editMode={editMode}>
        {scoreCards.length < 5 && 
        <Card bgColor="lightgray" grid shadow={false}>
          <Button 
            rounded 
            grid
            minWidth="1.5em" 
            minHeight="1.5em"
            padding=".4em .3em" 
            disabled={loading}
            onClick={addModal.open}
           >
            <i className={loading? "fa fa-spinner spin" : "fa fa-plus"}></i>
          </Button>
        </Card>}
      </DraggableArea>
    </div>);
}

export default ScoreCards;