import React, { useState, useEffect } from "react";
import Layout from "../../layout";
import { Card, Grid, Button, Modal, Switch, Empty } from "../../style";
import { Table } from "antd";
import { primaryColor, mobileWidth, gray } from "../../config";
import img from "../../assets/announ.svg";
import styled from "styled-components";
import { Announcement } from "../../model/domain/announcements";
import { useSelector, useDispatch } from "react-redux";
import { getAnnouncements, addAnnouncement, deleteAnnouncement, editAnnouncement } from "../../store/actions/announcement";
import Form from "./form";
import columns from "./columns";
import bg from '../../assets/login-bg.svg';

let locale = {
  emptyText: <Empty text="No Annoucement" />
};
const states = {
  ACTIVE: "ACTIVE",
  ALL: "ALL",
  EXPIRED: "EXPIRED",
  DEFAULT: "ACTIVE",
  states: ["ACTIVE", "ALL", "EXPIRED"],
};

const AnnoucementEl = () => {
  const eId = useSelector((state: any) => state.user.enterprise.id);
  const announcements = useSelector(
    (state: any) => state.announcement.announcements
  );
  const dispatch = useDispatch();
  const loadingAction = useSelector(
    (state: any) => state.announcement.loadingAction
  );
  const loadingAnnouncements = useSelector(
    (state: any) => state.announcement.loadingAnnouncements
  );

  const [state, setState] = useState({ active: states.DEFAULT });
  const add = (item: Announcement) => dispatch(addAnnouncement(eId, item));
  const edit = (item: Announcement) => dispatch(editAnnouncement(eId, item));

  const AddModal = Modal({
    title: "Add Announcement",
    message: <Adder add={add} close={() => AddModal.close()} />,
    hideCancel: true,
  });

  const open = (selected: Announcement) => {
    const EditModal = Modal({
      title: "Edit Announcement",
      message: <Adder
        add={edit}
        initialValues={selected}
        close={() => EditModal.close()}
      />,
      hideCancel: true,
    });
    EditModal.open();
  }

  const currentAnnouncements = announcements.filter(
    ({ status }: any) =>
      status.toUpperCase() === state.active || state.active === states.ALL
  );

  useEffect(() => {
    if (eId) dispatch(getAnnouncements(eId));
  }, [eId]);

  return (
    <Layout sidebar background={bg}>
      <Grid
        gridGap="0"
        className="left-on-desktop"
        placeItems="start center"
      >
        <Button
          style={{ placeSelf: "end" }}
          rounded
          padding=".2em"
          shadow
          onClick={() => AddModal.open()}
          minWidth="4.5em"
        >
          <i className={`fa fa-${"plus"}`}></i> Add
        </Button>
        <Grid
          placeItems="start stretch"
          background={gray}
          padding="1em"
          margin="1em 0"
          height="100%"
        >
        <Switch
          active={state.active}
          items={states.states}
          onClick={(active: string) => setState({ ...state, active })}
        />

        <Card shadow minWidth="60vw">
          <DIV>
            <Table
              locale={locale}
              key={Math.random() * 1000}
              loading={loadingAnnouncements}
              style={{ color: primaryColor, maxWidth: "90vw" }}
              dataSource={currentAnnouncements}
              columns={columns(open,
                (i: any) =>
                  dispatch(deleteAnnouncement(eId, i)), loadingAction
              )}
              summary={(pageData) => <Table.Summary.Row
                key={Math.random() * 1000}
                className="bold"
                style={{ color: primaryColor }}
              >
                <br />
                Total Annoucements: {pageData.length}
              </Table.Summary.Row>
              }
            />
          </DIV>
        </Card>
        </Grid>
      </Grid>
    </Layout>
  );
};

const DIV = styled.div`
  @media (max-width: ${mobileWidth}) {
    overflow-x: scroll;
  }
`;

const Adder = (props: any) => {
  return (
    <Grid customCols="3fr 2fr" width="100%" height="100%" gridGap="0">
      <div style={{ background: primaryColor, height: "100%", padding: "1em", width: "100%" }}>
        <img src={img} style={{ maxWidth: "300px" }} />
      </div>
      <Form {...props} />
    </Grid>
  );
};

export default AnnoucementEl;
