import React, { useState, useEffect, useContext } from "react";
import Layout from "../../layout";
import { Card, Grid, Switch, Empty, Button } from "../../style";
import { Table } from "antd";
import { primaryColor, mobileWidth, gray } from "../../config";
import styled from "styled-components";
import { useSelector } from "react-redux";
import { useEnterprise } from "../../hooks/enterprise";
import columns from "./columns";
import bg from "../../assets/login-bg.svg";
import { ComponentsContext } from "../../context/components";
import { useWidgets } from "../../hooks/widget";
import ScoreCards from "../../components/score-card/score-cards";

const states = {
  ACCEPTED: "Enabled",
  ALL: "ALL",
  DEFAULT: "ALL",
  PENDING: "Onboarding",
  states: ["Enabled", "ALL", "Onboarding"],
};

let locale = {emptyText: <Empty text="No Users" />};

const Controls = () => {
  const eId = useSelector(({ user }: any) => user.enterprise.id);
  const { editMode, toggleEditMode } = useContext(ComponentsContext);
  const screen = "adminControl";
  const resource = "enterpriseUser";

  const {
    loading,
    scoreCards,
    scoreCardOptions,
    editScoreCards,
    removeScoreCard,
  } = useWidgets({ eId, screen, resource });

  const {
    enterpriseUsers,
    loading: loadingUsers,
    acceptEnterpriseUser,
    removeEnterpriseUser,
    rejectEnterpriseUser,
  } = useEnterprise({ eId });

  const [state, setState] = useState({
    active: states.DEFAULT,
  });

  const current = enterpriseUsers.filter(
    ({
      user: {
        apps: { enterpriseApp },
      },
    }: any) =>
      enterpriseApp === state.active.toLowerCase() ||
      state.active === states.ALL
  );

  return (
    <Layout sidebar background={bg}>
      <Grid gridGap="0" className="left-on-desktop" placeItems="start center">
        <Button
          rounded
          shadow
          placeSelf="end"
          margin=".4em"
          onClick={toggleEditMode}
          minWidth="4.5em"
        >
          <i className={`fa fa-${!editMode ? "edit" : "check"}`}></i>
        </Button>
        <ScoreCards
          loading={loading}
          removeScoreCard={removeScoreCard}
          editScoreCards={editScoreCards}
          scoreCards={scoreCards}
          scoreCardOptions={scoreCardOptions}
        />
        <Grid
          placeItems="start stretch"
          background={gray}
          padding="1em"
          margin="1em 0"
          height="100%"
        >
          <Switch
            active={state.active}
            items={states.states}
            onClick={(active: string) => setState({ ...state, active })}
          />
          <Card shadow minWidth="60vw">
            <DIV>
              <Table
                locale={locale}
                rowKey={(record) => record.user.id}
                loading={loadingUsers}
                style={{ color: primaryColor, maxWidth: "90vw" }}
                dataSource={current}
                columns={columns(
                  acceptEnterpriseUser,
                  rejectEnterpriseUser,
                  removeEnterpriseUser,
                  loading
                )}
              />
            </DIV>
          </Card>
        </Grid>
      </Grid>
    </Layout>
  );
};

const DIV = styled.div`
  @media (max-width: ${mobileWidth}) {
    overflow-x: scroll;
  }
`;

export default Controls;
