import React, { CSSProperties, useEffect, useState, useMemo } from 'react'
import { Group } from '@vx/group';
import { TimeSeries } from '../../model/domain/metrics';
import * as axis from './axis';
import * as scale from './scale';
import { DrawArea } from './drawArea';
import { primaryColor } from '../../config';

export interface LineChartProps {
  height: number;
  width: number;
  timeSeriesResult: TimeSeries.Result;
  metricDescriptor: TimeSeries.MetricDescriptor;
  backgroundColor: string;
}

const yAxisProps = (tickFormat: Function) => ({
  hideAxisLine: true,
  tickStroke: primaryColor,
  tickLength: 6,
  tickLabelProps: () => ({
    fontWeight: 'bold',
    textAnchor: 'end',
    fill: primaryColor, 
    fontSize: 12,
    dy: 5,
    dx: -2
  }),
  tickFormat
})

const xAxisProps = (tickFormat: Function) => ({
  hideAxisLine: true,
  tickStroke: primaryColor,
  tickLength: 6,
  tickLabelProps: () => ({
    fill: primaryColor, 
    fontSize: 12, 
    textAnchor: 'middle',
    fontWeight: "bold",
  }),
  tickFormat
});

function getRects({height, width}: { height: number; width: number; }) {
  const yAxisRect = { left: 0, top: 0, height, width: 35 };

  //padding right;
  const p = 10;

  const xAxisRect = {
    left: yAxisRect.width,
    top: height - 30,
    height: 30,
    width: width - yAxisRect.width - p
  };

  const drawAreaRect = {
    left: yAxisRect.width,
    top: 0,
    height: height - xAxisRect.height,
    width: width - yAxisRect.width - p
  }

  return {
    yAxis: yAxisRect,
    xAxis: xAxisRect,
    drawArea: drawAreaRect
  };
}

export function LineChart({height, width, timeSeriesResult, metricDescriptor, backgroundColor}: LineChartProps) {

  const rects = getRects({height, width});
  
  const xScale = scale.getXScale(rects.drawArea.width, timeSeriesResult);
  const yScale = scale.getYScale(rects.drawArea.height, timeSeriesResult);

  const axisBottom = axis.createXAxis(xScale.scale, xAxisProps(xScale.tickFormat));
  const axisLeft = axis.createYAxis(yScale.scale, yAxisProps(yScale.tickFormat));

  const drawArea = (
    <DrawArea
      backgroundColor={backgroundColor}
      width={rects.drawArea.width}
      height={rects.drawArea.height}
      timeSeriesResult={timeSeriesResult}
      metricDescriptor={metricDescriptor}
      dateScale={xScale.scale}
      yScale={yScale.scale} />
  );
  
  return (
    <div style={{height: height + 'px', width: width + 'px', position: 'relative', backgroundColor}}>
        <svg
          style={{position: 'absolute', top: rects.yAxis.top, left: rects.yAxis.left}}
          width={rects.yAxis.width}
          height={rects.yAxis.height}>
            <Group left={rects.yAxis.width}>
              {axisLeft}
            </Group>
        </svg>

        <svg
          style={{position: 'absolute', top: rects.xAxis.top, left: rects.xAxis.left}}
          width={rects.xAxis.width}
          height={rects.xAxis.height}>
            <Group left={0}>
              {axisBottom}
            </Group>
        </svg>

        <div style={{position: 'absolute', top: rects.drawArea.top, left: rects.drawArea.left}}>
          {drawArea}
        </div>
    </div>
  );
}