
import React from 'react';
import styled, { keyframes } from 'styled-components';
import { Avatar } from '.'
import { mobileWidth, primaryColor } from '../config';
import { useLocation } from "react-router";

const TopNavBar = ({ user, logout }: any) => {
  const location = useLocation().pathname;
  const avatarImg = "https://res.cloudinary.com/ssaran97/image/upload/v1593738437/enterprise/spartan_logo.png"
  return (
    <MiniNav>
      <Text className="capitalize bold txt-md txt-left" style={{ minWidth: "100px", width: "100%" }}>
        {location === "/" || location === "/overview" ? "Overview" : location.replace("/", "")}
      </Text>

      <div className="grid">
        <Avatar url={avatarImg} />
      </div>
    </MiniNav>)
}

const Text = styled.div`
    padding-left: 10em;
    @media(max-width: ${mobileWidth}){
       padding-left: 0;
       font-size: 1.2em;
    } 
  `;

const MiniNav = styled.div`
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  position: fixed;
  font-size: 15px;
  top: 0;
  right : 0;
  left: 0;
  padding: 0 1.5em;
  z-index: 9;
  width: 100vw;
  height: 4em; 

  background: white;  
  color: ${primaryColor};

  display: grid;
  grid-gap: 1em;
  grid-template-columns: 1fr auto auto;
  grid-template-rows: 1fr;
  place-items: center end;
    @media(max-width: ${mobileWidth}){
      top : inherit;
      padding-left: 0
      grid-gap: .5em;
    } `;

export default TopNavBar;