import React, { useState } from "react";
import { MenuItem, Grid  } from "../../style";
import moment, { Moment } from "moment";
import timeframeOptions from './timeframes';
import { Popconfirm } from "antd";
import FilterBox from "./filter-box";

interface SetTime {
  (arg: { startTime: string | Moment, endTime: string | Moment }): void
}
const Filter = (props: any) => {
  const { interval: initialState, setInterval, backgroundColor, color, loading, hide } = props;
  const [active, setActive] = useState("DAY");

  const [state, setState] = useState(initialState);

  const setTime: SetTime = (props) => {
    const { startTime, endTime } = props;
    const shouldSwap =  moment(startTime) > moment(endTime);
    console.log(props)
    const state = {
      startTime: moment(shouldSwap ? endTime : startTime).toISOString(),
      endTime: moment(shouldSwap ? startTime : endTime).toISOString()
    };
    setState(state);
  };

  const submit = () => {
    setInterval(state);
    setActive('CUSTOM');
  }

  if (hide) return null;
  return (
    <Grid customCols="auto auto" gridGap=".2em">
      <span className="hide-mobile" style={{ padding: "0 .5em" }}>{
        timeframeOptions.map(({ id, label, calc }) => (
          <MenuItem
            active={id === active}
            key={id}
            onClick={() => {
              setActive(id)
              setInterval(calc());
            }}
          >{label}</MenuItem>))
      }
      </span>

      <Popconfirm
        placement="bottom"
        icon=""
        title={
          <FilterBox
            submit={submit}
            interval={state}
            backgroudColor={backgroundColor}
            color={color}
            setTime={setTime}
          />
        }
        onConfirm={submit}
        okButtonProps={{size: 'middle'}}
        cancelButtonProps={{size: 'middle'}}
        okText={"Generate Data"}
        cancelText=""
      >
        <MenuItem
          className="shadow"
          active={active === 'CUSTOM'}
          style={{
            padding: ".2em .45em",
            borderRadius: "50%",
            fontSize: "1.4em",
          }}
          onClick={(e) => e.preventDefault()}>
          <i className={`fa fa-${loading ? 'spinner spin' : 'filter'}`}></i>
        </MenuItem>
      </Popconfirm>
    </Grid>
  );
};

export default Filter;
