import React, { useRef, useEffect } from "react";
import { Text, Grid } from "../../style";
import { DatePicker, TimePicker } from "antd";
import { moment, Moment, FORMAT_DATETIME } from '../../utils/time';
import { primaryColor } from "../../config";

const FilterBox = (props: any) => {
  const { interval, setTime, backgroundColor, color } = props;
  const disabledDate = (cur: Moment | null) => cur > moment();

  return (
    <div
      style={{
        display: "grid",
        height: "100%",
        backgroundColor,
        color,
        placeItems: "end start",
      }}
    >
      <Text width="100%" textAlign="center" size={2} bold>
        Filter
      </Text>

      <Text color={primaryColor} textAlign="left" margin=".5em 0 0 0" bold>
        Start Date
      </Text>
      <Grid cols="2">
        <DatePicker
          disabledDate={disabledDate}
          style={{ borderRadius: 8 }}
          onChange={(e: Moment | null) => {
            if (!e) return;
            console.log(e.toString())
            setTime({...interval, startTime: e.toISOString()});
          }}
          value={moment(interval.startTime)}
        />

        <TimePicker
          disabledDate={disabledDate}
          use12Hours
          minuteStep={15}
        
          style={{ borderRadius: 8 }}
          value={moment(interval.startTime)}
          onChange={(e: Moment | null) => {
            if (!e) return;
            setTime({...interval, startTime: e.toISOString()});
          }}
        />
      </Grid>
      <Text color={primaryColor} bold margin=".5em 0 0 0">
          End Date
      </Text>
      <Grid cols="2">
        <DatePicker
          disabledDate={disabledDate}
          style={{ borderRadius: 8 }}
          onChange={(e: Moment | null) => {
            if (!e) return;
            setTime({...interval, endTime: e.toISOString()})
          }}
          value={moment(interval.endTime)}
        />

        <TimePicker
          disabledDate={disabledDate}
          use12Hours
          minuteStep={15}
          style={{ borderRadius: 8 }}
          value={moment(interval.endTime)}
          onChange={(e: Moment | null) => {
            if (!e) return;
            setTime({...interval, endTime: e.toISOString()});
          }}
        />
      </Grid>
    </div>
  );
};


export default FilterBox;
