import React, { useState  } from 'react';
import { Grid, Card, Button, Modal } from '../../../style';
import { XyChart } from '../../../model/domain/dashboard';
import { TimeSeries } from '../../../model/domain/metrics';
import ChartForm from './chart-form';

const ChartAdder = (props: { submit: (el: XyChart) => void, eId: string }) => {
    const AdderModal = Modal({
        title: "Create Your Chart",
        hideCancel: true,
        message: <MultiStep  close={() => AdderModal.close()} {...props} />
    })

    return (
        <Card
            bgColor="lightgray"
            style={{ boxSizing: "border-box" }}
            width="95%"
            grid shadow={false}
            placeSelf="start"
        >
            <Button
                rounded
                minWidth="1.5em"
                minHeight="1.5em"
                padding={'.7em .5em'}
                onClick={AdderModal.open}>
                <i className="fa fa-plus"></i>
            </Button>
        </Card>)
}

const MultiStep = ({ submit, close, eId }: any) => {
    const [state, setter] = useState({
        step: 0,
        completed: false,
        type: "",
        selectedType: "",
    });

    const setState = (newState: any) => setter({ ...state, ...newState })
    type ChartType = 'lineChart';
    let defaultProps = {
        title: "",
        timeSeriesQuery: {
            metricType: "garage_occupancy_count",
            resourceIds : [],
            crossSeriesReducer: 'SUM',
            alignment: null
        },
        backgroundColor: "#FFFFFF",
        xAxisLabel: "T",
        yAxisLabel: "Occupancy",
    };

    const [ chart, chartSetter ] = useState<XyChart | any>({type: ""});

    const setChart = (obj: any, type = state.type as ChartType) => {
      const prevValues = chart[type] || {};
      const crossSeriesReducer = state.selectedType === 'lineChart'? 'SUM' : 'NONE';
      
      const nextValue = {
        type,
        [type]: {
          ...prevValues,
          ...obj,
          crossSeriesReducer
        },
      };
      chartSetter(nextValue);
    };

    const setType = (selectedType: string) => {
        const isMulti = selectedType === 'multiLineChart';
        const type  = isMulti? 'lineChart' : selectedType as ChartType;
        setState({ completed: true, type, selectedType });
            defaultProps.timeSeriesQuery.crossSeriesReducer = isMulti? 'NONE' : 'SUM';
            defaultProps.timeSeriesQuery.alignment = isMulti? {
                alignmentPeriod: 60,
                alignmentReducer: 'MEAN'
            } : null;
        setChart(defaultProps, type);
    }

    const setTitle = (title: string) => {
        setChart({ title })
        setState({ completed : title.length > 0});
    }

    const setTimeseries = (props: any) => {
        const { timeSeriesQuery } = chart[chart.type];
        setChart({
            timeSeriesQuery: {
                    ...timeSeriesQuery,
                    ...props
            },
        });
    }

    const setAlignment = (alignmentPeriod : number | null) => {
        const { alignment: prevAlignment } = chart[chart.type].timeSeriesQuery;
        const alignment = alignmentPeriod !== null ? {
            ...prevAlignment,
            ...prevAlignment?.alignmentReducer? {} : { alignmentReducer : 'MEAN'}, 
            alignmentPeriod
        } : null;
        setTimeseries({ alignment  });
    }

    const setGarageIds = (resourceIds = []) => setTimeseries({ resourceIds });
    
    const setArgs = (alignmentReducer: TimeSeries.CrossSeriesReducerFunction) => {
        const { alignment } = chart[chart.type].timeSeriesQuery;
        setTimeseries({ alignment: { 
            ...alignment,
            alignmentReducer
        } });
    }

    const setMetricType = (metricType: string) => setTimeseries({ metricType });

    const forward = () => {
        if (state.step < InputComponents.length) {
            setState({ step: state.step + 1 , completed : false});
        }
    }

    const finish = () => { 
        submit(chart);
        close();
    }

    const InputComponents = [
        <ChartForm.Type
            setType={setType}
            type={state.selectedType}
        />,
        <ChartForm.Info
            isMulti={state.selectedType === 'multiLineChart'}
            eId={eId}
            setMetricType={setMetricType}
            {...{chart: chart[state.type]}}
            setTitle={setTitle}
            setArgs={setArgs}
            setAlignment={setAlignment}
            setGarageIds={setGarageIds}
        />,
        <ChartForm.Confirm finish={finish} />]
    return <>
        <div>
            {InputComponents[state.step]}
        </div>
        <Grid width={"100%"} placeItems="end">
            <Button onClick={forward}
            style={{margin: "0 1em 1em 0"}} 
            shadow disabled={!state.completed}>Next &nbsp;<i className="fa fa-chevron-right" aria-hidden="true"></i> </Button>
        </Grid>
    </>;
}

export default  ChartAdder;