import React, { useState, useEffect } from 'react'
import styled from 'styled-components';
import { Card, Grid, } from '../../style';
import CountUp from 'react-countup';
import { Radio } from 'antd';
import API from '../../api';
import { useSelector } from 'react-redux';

const intoHeadings = (str: string): string => {
  //if (!intoHeadings || intoHeadings.length < 2) return String(str);
  return str.charAt(0).toUpperCase() + str.substring(1).replace(/ /g, '&nbsp;');
}

export const ScoreCard = React.forwardRef(({ name, bg, editMode, remove, index, flatValueQuery }: any, ref: any) => {
  const [value, setValue] = useState<number | 'N/A'>(0);
  const eId = useSelector((state: any) => state.user.enterprise.id);
  useEffect(() => {
    API.getFlatValue(eId, flatValueQuery)
      .then((res: any) => {
        if (res?.success) {
          setValue(res.flatValueResult.flatValue.value || 0)
        } else throw new Error("Failed")
      })
      .catch(() => setValue("N/A"))
  }, []);


  return (
    <Card
      shadow
      hover
      height="100%"
      className="noselect"
      color="white"
      style={{ background: bg, cursor: "pointer", backgroundSize: "cover" }}
    >
      <div className="txt-center bold" style={{ fontSize: ".9em", color: "white", fontWeight: "bold" }}>
        <span style={{ textShadow: "1px 1px gray" }} dangerouslySetInnerHTML={{ __html: String(intoHeadings(name)) }} />
        {editMode ? <i className="fa fa-trash right txt-shadow txt-md red" onClick={() => remove(index)}></i>
          : <i className="fas fa-money-bill-alt right txt-shadow txt-md" ></i>}
      </div>
      <Number className="grid-box bold" >{
        typeof value === "number" ?
          <CountUp duration={2} start={0} end={value as number} /> : value
      }
      </Number>
    </Card>
  )
});

export const OptionCard = ({ title, bg, selected, onClick, el, color }: any) => (
  <Card
    onClick={onClick}
    hover
    shadow
    grid
    width="100%"
    padding=".5em"
    className="noselect pointer"
    color="white"
    style={{ background: bg, backgroundSize: "cover", fontSize: '.9em' }}
  >
    <Grid bold className="txt-center" size={3} color={color} customCols={"auto 1fr"}>
      <Radio.Group value={selected ? 1 : 0}>
        <Radio value={1}></Radio>
      </Radio.Group>
      <span dangerouslySetInnerHTML={{ __html: String(intoHeadings(title)) }} />
    </Grid>
    {el}
  </Card>
);

const Number = styled.div`
  font-size: 60px;
  color: white;`;
