import React, { useState, useEffect } from 'react';
import { Grid, Text } from '../../../style';
import { OptionCard } from '../../../components/score-card/score-card';
import { Input, Progress, message, Select } from 'antd';
import bargraph from '../../../assets/bargraph.jpg';
import lineChart from '../../../assets/linechart.jpg';
import multiLineChart from '../../../assets/multiLineChart.jpg';
import { primaryColor } from '../../../config';
import useGarages from '../../../hooks/garage';
import { Garage } from '../../../model/domain/garage';
import img from "../../../assets/announ.svg";

const ChartSelector = ({ setType, type }: { setType: (el: any) => void, type: any }) => {
    const charts = [
        {
            id: "lineChart",
            label: "Area Chart",
            img: lineChart
        },
        {
            id: "multiLineChart",
            label: "Multi Line Chart",
            img: multiLineChart
        },
        // {
        //     id: "stackedGraph",
        //     label: "Bar Graph",
        //     img: bargraph
        // }
    ]
    return (
        <Grid mobileCols="auto auto" customCols="auto auto auto">
            {charts.map(({ id, label, img }) => (
                <OptionCard
                    bg="linear-gradient(-30deg, white 10%, white 100%)"
                    el={<img style={{ maxHeight: "150px", minHeight: "100px", maxWidth: "30vw" }} src={img} />}
                    onClick={() => setType(id)}
                    selected={id === type}
                    color={primaryColor}
                    title={label}
                />))}
        </Grid>
    )
}

const InfoSelector = ({ eId, isMulti, setGarageIds, chart, setMetricType, setTitle, setArgs, setAlignment }: any) => {
    const { garages, loading } = useGarages({ eId });
    const { Option } = Select;
    const props = { style: { width: '400px', maxWidth: "70vw" } };
    const args = [
        {
            value: 'MEAN',
            label: 'Average'
        },
        {
            value: 'MAX',
            label: 'Max'
        },
        {
            value: 'MIN',
            label: 'Min'
        },
    ]
    const metricTypes = [
        {
            value: 'garage_occupancy_count',
            label: 'Garage Occupancy Count'
        },
        {
            value: 'garage_occupancy_percent',
            label: 'Garage Occupancy Percent'
        }
    ];

    const alignments = [
        {
            value: null,
            label: 'Auto'
        },
        {
            value: 60 * 24,
            label: 'Daily'
        },
        {
            value: 60,
            label: 'Hourly'
        }
    ]

    return (
        <div style={{ padding: ".3em" }}>
            <div style={{ marginBottom: "1em" }}>
                <div className="txt-shadow  txt-left">Pick a name for your Chart:</div>
                <Input
                    placeholder="Ex: Garage 1 Occupancy"
                    onChange={(e) => setTitle(e.target.value.trim())}
                />
            </div>

            <div style={{ marginBottom: "1em" }}>
                <div className="txt-shadow txt-left">Pick Measurement</div>
                <Select
                    {...props}
                    placeholder="Select"
                    value={chart.timeSeriesQuery.metricType}
                    onChange={(e) => setMetricType(e)}
                    loading={loading}
                >
                    {metricTypes.map(({ label, value }) => (
                        <Option
                            className='capitalize'
                            value={value}
                        >
                            {label}
                        </Option>))
                    }
                </Select>
            </div>
            {isMulti &&
                <>
                    <div style={{ marginBottom: "1em" }}>
                        <div className="txt-shadow txt-left">Pick Aggregators</div>
                        <Select
                            onChange={(e) => setArgs(e)}
                            {...props}
                            disabled={chart?.timeSeriesQuery?.alignment === null}
                            value={chart?.timeSeriesQuery?.alignment?.alignmentReducer || 'MEAN'}
                        >
                            {args.map(({ label, value }) => <Option value={value}>{label}</Option>)}
                        </Select>
                    </div>
                    <div style={{ marginBottom: "1em" }}>
                        <div className="txt-shadow txt-left">Pick Interval</div>
                        <Select
                            onChange={(e) => setAlignment(e)}
                            {...props}
                            value={chart?.timeSeriesQuery?.alignment?.alignmentPeriod || null}
                        >
                            {alignments.map(({ value, label }) => <Option value={value}>{label}</Option>)}
                        </Select>
                    </div>
                </>
            }

            <div style={{ marginBottom: "1em" }}>
                <div className="txt-shadow txt-left">Pick Garage(s)</div>
                <Select
                    {...props}
                   
                    mode="multiple"
                    placeholder="All Garages"
                    value={chart.timeSeriesQuery.resourceIds}
                    onChange={(e) => setGarageIds(e)}
                    loading={loading}
                >
                    {garages.map(({ name, id }: Garage) => <Option value={id}>{name}</Option>)}
                </Select>
            </div>

        </div>
    )
}

const Confirmation = ({ finish }: any) => {
    const [count, setCount] = useState(0);

    useEffect(() => {
        const interval = setInterval(() => setCount(count => count + 5), 300);

        return () => clearInterval(interval);
    }, []);

    useEffect(() => {
        if (count >= 100) {
            finish();
            message.success('Your Changes was saved');
        }
    }, [count]);


    return (
        <Grid width={"100%"}>
            <Progress percent={count} style={{ width: 400, maxWidth: "80vw" }} />
            <Text bold color="white">
                {count < 30 ? "Creating " : "Setting up "}
                your Chart</Text>
        </Grid>
    )
}

export default {
    Type: ChartSelector,
    Info: InfoSelector,
    Confirm: Confirmation
};