import React  from "react";
import { XyChart } from "../../model/domain/dashboard";
interface Editable {
  editable: boolean;
  eId: string;
  remove: () => void;
}

const withFilter = (WrappedComponent: any) => {
  return (_: XyChart & Editable) => {
    return <div>
      <WrappedComponent />
    </div>
  };

}

export default withFilter;
