import React, { Fragment } from 'react';
import { Grid, Placeholder } from '../../../style';
import { getComponents } from './util/chart-builder';
import ChartAdder from './chart-adder';
import { XyChart } from '../../../model/domain/dashboard';
import { gray } from '../../../config';
import { useWidgets } from '../../../hooks/widget';
import { useSelector } from 'react-redux';
import { statefulList } from '../../../utils/list';

const Analytics = () => {
  const eId = useSelector((state: any) => state.user.enterprise.id);
  const { charts, loading, addWidget, removeChart } = useWidgets({ eId, screen: "garage", resource: "garage" });

  return (
    <Grid placeItems="start stretch" background={gray} padding="1em" height="100%">
      <Grid cols="2">
        {
          statefulList({
            loading,
            loader: <Placeholder
            lines={7}
            number={4}
            style={{ maxWidth: "500px", minHeight: "400px", minWidth: "100%" }}
          />,
            empty: <></>,
            list: charts,
            map: ((item: XyChart, i: number) => <Fragment key={i}>{getComponents(item, () => removeChart(i), eId)}</Fragment>)
          })
        }
        {!loading && <ChartAdder eId={eId} submit={(e: XyChart) => addWidget(e)} />}
      </Grid>
    </Grid>
  );
}

export default Analytics;
