import React from 'react';
import { ChartWithControl as LineChart } from '../../../../components/line-chart/chartWithControl';
import StackedGraph from '../stacked-graph';
import { XyChart } from '../../../../model/domain/dashboard';

export const getComponents = (El: XyChart, remove : any, eId: string, span = 1) =>{
    const isEditable = (El : any)=> El.editable !== false;
    switch(El.type){
        case "lineChart":   
        return  <LineChart {...El.lineChart} span={span} editable={isEditable(El)} remove={remove} />

        default:
        return<StackedGraph {...El}  eId={eId} editable={isEditable(El)} remove={remove} />
    }
}