import { moment } from '../../utils/time';

export const timeframeOption = [
  {
    id: "DAY",
    label: "1D",
    calc: () => {
      const endTime = moment();
      const startTime = endTime.clone()
        .set('hours', 0)
        .set('minute', 0)
        .set('second', 0)
        .set('millisecond', 0);

      return { endTime, startTime };
    }
  },
  {
    id: "3_DAYS",
    label: "3D",
    calc: () => {
      const endTime = moment();
      const startTime = endTime.clone().add(-3, 'day');

      return { startTime, endTime };
    }
  },
  {
    id: "WEEK",
    label: "1W",
    calc: () => {
      const endTime = moment();
      const startTime = endTime.clone().add(-7, 'day');

      return { startTime, endTime };
    }
  }, {
    id: "MONTH",
    label: "1M",
    calc: () => {
      const endTime = moment();
      const startTime = endTime.clone().add(-1, 'month');

      return { startTime, endTime };
    }
  },
  {
    id: "3_MONTHS",
    label: "3M",
    calc: () => {
      const endTime = moment();
      const startTime = endTime.clone().add(-3, 'month');

      return { startTime, endTime };
    }
  },
  {
    id: "YEAR",
    label: "1Y",
    calc: () => {
      const endTime = moment();
      const startTime = endTime.clone().add(-1, 'year');

      return { startTime, endTime };
    }
  }
];

export default timeframeOption;