import React, { useState } from 'react'
import styled from 'styled-components';
import { Grid, Button, Text, Empty } from '../../style';
import { primaryColor } from '../../config';
import { OptionCard } from './score-card';
import { ScoreCard } from '../../model/domain/dashboard';
import { FlatValue as FV } from '../../model/domain/metrics';
import { statefulList } from '../../utils/list';

interface ScoreCardEditor {
  currentScoreCard: ScoreCard[];
  setCurrent: any;
  options: FV.MetricDescriptor[];
  close: any;
};

const AddScoreCard = ({ currentScoreCard, setCurrent, options, close }: ScoreCardEditor) => {
  const NUM_CARDS = 5;
  const [cards, setCards] = useState<ScoreCard[]>([]);
  const remainingCards = NUM_CARDS - currentScoreCard.length - cards.length;

  const hasCard = (scoreCard: ScoreCard[], option: FV.MetricDescriptor) => {
    return scoreCard.reduce((acc: boolean, card: ScoreCard) => {
      return acc || card.title === option.displayName;
    }, false)
  };

  const unselectedOptions = options.filter((item: FV.MetricDescriptor) => !hasCard(currentScoreCard, item))

  const select = (value: FV.MetricDescriptor) => setCards([...cards, buildScoreCard(value)]);

  const unselect = (value: FV.MetricDescriptor) => setCards(cards.filter((el: ScoreCard) => el.title !== value.displayName))

  const buildScoreCard = (item: FV.MetricDescriptor): ScoreCard => {
    return {
      title: item.displayName,
      icon: "",
      flatValueQuery: {
        metricType: item.type
      },
      backgroundColor: "linear-gradient(-30deg, #1ecb7d 10%, rgba(30, 203, 180, 0.7) 100%)"
    }
  }
  const confirm = () => {
    setCurrent([...currentScoreCard, ...cards]);
    close();
  }

  return (<div>
    <Text bold color={primaryColor} textAlign={"center"}>Select {remainingCards} more Score Cards</Text>
    <Grid customCols="repeat(4, auto)" gridGap={'.6em'}>
      {statefulList({
        emptyText: "No More Options",
        list: unselectedOptions,
        map: (item: any) => {
          const { displayName, backgroundColor } = item;
          const selected = hasCard(cards, item);
          return (
            <OptionCard
              onClick={() => selected ? unselect(item) : select(item)}
              title={displayName}
              key={displayName}
              el={<Number className="grid-box bold">10</Number>}
              value={10}
              color={"white"}
              bg={backgroundColor || "linear-gradient(-30deg, #1ecb7d 10%, rgba(30, 203, 180, 0.7) 100%)"}
              selected={selected}
            />
          )
        }
      })}
    </Grid>
   
    <Button shadow onClick={() => {
      setCards(currentScoreCard)
      close()
    }}>Cancel</Button>
     <Button shadow onClick={() => confirm()}>Confirm</Button>
  </div>)
}

const Number = styled.div`
    font-size: 60px;
    color: white;`;

export default AddScoreCard;