import React from "react";
import { Garage } from "../../../model/domain/garage";
import { Grid, Button, Modal, Placeholder, Empty } from "../../../style";
import useGarages from "../../../hooks/garage";
import { useSelector } from "react-redux";
import { gray } from "../../../config";
import GarageCard from "./garage-card";
import { GarageMultiStep } from "./garage-add";
import { GarageEditor } from "./garage-edit";
import { statefulList } from "../../../utils/list";
import { useOccupanyTable } from "../../../hooks/occupancy-table";

const Listings = () => {
  const eId = useSelector((state: any) => state.user.enterprise.id);
  const { garages, loading, refresh } = useGarages({ eId });
  const { tables,  getTableById } = useOccupanyTable({ eId });

  const props = { eId, tables, refresh };

  const AddModal = Modal({
    title: "Add Your Garage",
    message: <GarageMultiStep close={() => AddModal.close()} {...props}  />,
    hideCancel: true,
  });

  const edit = (garage: Garage) => {
    const EditGarage = Modal({
      title: "Edit Your Garage",
      message: <GarageEditor close={() => EditGarage.close()}  {...props} garage={garage} />,
      hideCancel: true,
    });
    EditGarage.open();
  }

  return (
    <Grid placeItems="start stretch" background={gray} padding="1em" height="100%">
      <Grid>
        <div className="grid">
          <Button
            style={{ placeSelf: "end" }}
            margin="1em"
            rounded
            disabled={loading}
            shadow
            onClick={() => AddModal.open()}
            minWidth="4.5em"
          >
            <i className='fa fa-plus'></i> Add
          </Button>
          {statefulList({
            loading,
            loader: <Placeholder number={5} style={{ maxWidth: "600px", minWidth: "40vw" }} />,
            list: garages,
            map: (g: Garage) => <GarageCard {...g} edit={() => edit(g)} getTableById={getTableById}/>
          })}
        </div>
      </Grid>
    </Grid>
  );
};

export default Listings;
